/*! demo css*//*! reset; update: 2015-7-30*/
@import url(../node_modules/vue-toast-mobile/lib/index.css);
article,aside,details,figcaption,figure,footer,header,hgroup,main,nav,section,summary{display:block
}
audio,canvas,progress,video{display:inline-block;vertical-align:baseline
}
body,h1,h2,h3,h4,h5,h6,p,ul,form,img,input{margin:0;padding:0
}
*,*:before,*:after{box-sizing:border-box;-webkit-box-sizing:border-box
}
div,a{-webkit-tap-highlight-color:transparent
}
html{height:100%;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;text-size-adjust:100%;-webkit-font-smoothing:antialiased
}
body{font-size:14px;line-height:1.5;font-family:"Open Sans","Helvetica Neue",Helvetica,Arial,sans-serif;color:#494949;background-color:#fff;position:relative
}
ul{list-style:none
}
a{color:#333;text-decoration:none
}
a:visited{color:#333;text-decoration:none
}
img{border:none;vertical-align:middle;max-width:100%
}
.hide,[hidden]{display:none
}
input,textarea,select,button{font-size:100%;font-family:inherit;margin:0
}
button,html input[type="button"],input[type="reset"],input[type="submit"]{-webkit-appearance:none;-moz-appearance:none;appearance:none
}
button,input,select,textarea{font-size:100%;margin:0;vertical-align:baseline;line-height:normal
}
input[type="checkbox"],input[type="radio"]{box-sizing:border-box;padding:0
}
table{border-collapse:collapse;border-spacing:0;max-width:100%
}
blockquote{background:#C6C6C4;margin:0 5px 10px 5px;padding:10px;border-left:3px solid #797979
}
blockquote p{margin-bottom:0 !important
}
h1,h2,h3,h4,h5{color:#333;font-weight:normal
}
h1 a,h2 a,h3 a,h4 a,h5 a{font-family:inherit;color:#333
}
.clearfix:before,.clearfix:after{content:"";display:table
}
.clearfix:after{clear:both
}
.fl{float:left
}
.fr{float:right
}
.ta-c{text-align:center
}
.ta-r{text-align:right
}
.ta-l{text-align:left
}
.bfc{overflow:hidden
}
.container{margin:0 auto;position:relative;max-width:750px;min-width:320px;overflow:hidden
}
.container:after{content:".";display:block;height:0;clear:both;visibility:hidden
}
.inner{padding:0 20px;position:relative
}
.px-t{border-top:1px solid #f60
}
@media screen and (-webkit-min-device-pixel-ratio: 2){
.px-t{border-top:0;background-repeat:repeat-x;background-size:100% 1px;background-position:left top;background-image:-webkit-gradient(linear, left bottom, left top, color-stop(0.5, transparent), color-stop(0.5, #f60), to(#f60))
}
}
.px-b{border-bottom:1px solid #f60
}
@media screen and (-webkit-min-device-pixel-ratio: 2){
.px-b{border-bottom:0;background-repeat:repeat-x;-webkit-background-size:100% 1px;background-position:left bottom;background-image:-webkit-gradient(linear, left top, left bottom, color-stop(0.5, transparent), color-stop(0.5, #f60), to(#f60))
}
}
.px-tb{border-top:1px solid #f60;border-bottom:1px solid #f60
}
@media screen and (-webkit-min-device-pixel-ratio: 2){
.px-tb{border:0;background-repeat:repeat-x;background-size:100% 1px;background-image:-webkit-gradient(linear, left bottom, left top, color-stop(0.5, transparent), color-stop(0.5, #f60), to(#f60)),-webkit-gradient(linear, left top, left bottom, color-stop(0.5, transparent), color-stop(0.5, #f60), to(#f60));background-position:top, bottom
}
}
.px-l{border-left:1px solid #f60
}
@media screen and (-webkit-min-device-pixel-ratio: 2){
.px-l{border-left:0;background-position:left top;background-image:-webkit-gradient(linear, right top, left top, color-stop(0.5, transparent), color-stop(0.5, #f60), to(#f60));background-repeat:repeat-y;background-size:1px 100%
}
}
.px-r{border-right:1px solid #f60
}
@media screen and (-webkit-min-device-pixel-ratio: 2){
.px-r{border-right:0;background-position:right top;background-image:-webkit-gradient(linear, left top, right top, color-stop(0.5, transparent), color-stop(0.5, #f60), to(#f60));background-repeat:repeat-y;background-size:1px 100%
}
}
.px-all{border:1px solid #f60;border-radius:0
}
@media screen and (-webkit-min-device-pixel-ratio: 2){
.px-all{position:relative;border:0
}
.px-all:before{content:"";width:200%;height:200%;position:absolute;top:0;left:0;border:1px solid #f60;-webkit-transform:scale(0.5);-ms-transform:scale(0.5);transform:scale(0.5);-webkit-transform-origin:0 0;-ms-transform-origin:0 0;transform-origin:0 0;padding:1px;border-radius:0;pointer-events:none
}
}
.reg-title{margin:1.06667rem 0 1.6rem 0;font-size:28px;color:#fff
}
.form-list{height:1.8rem;padding:.53333rem 0 .26667rem 0;border-bottom:1px solid #fff;position:relative
}
@media screen and (-webkit-min-device-pixel-ratio: 2){
.form-list{border-bottom:0;background-repeat:repeat-x;-webkit-background-size:100% 1px;background-position:left bottom;background-image:-webkit-gradient(linear, left top, left bottom, color-stop(0.5, transparent), color-stop(0.5, #fff), to(#fff))
}
}
.get-code{background-color:#fff;position:absolute;right:0;top:.53333rem;color:#ccc;padding:3px 5px;border-radius:3px
}
.get-code.disabled{pointer-events:none;background-color:#ccc;color:#fff
}
.reg-input{background:none;border:none;color:#fff;font-size:16px;width:100%;padding:5px 0
}
.reg-input:focus{outline:none
}
.contry-code{background:#fff;font-size:16px;padding:2px 8px;color:#00a0a6;border-radius:3px;margin-right:5px
}
.reg-btn{width:100%;display:block;border:1px solid #fff;border-radius:100px;border-radius:50px;font-size:17px;text-align:center;padding:8px 0;margin:.53333rem 0
}
@media screen and (-webkit-min-device-pixel-ratio: 2){
.reg-btn{position:relative;border:0
}
.reg-btn:before{content:"";width:200%;height:200%;position:absolute;top:0;left:0;border:1px solid #fff;-webkit-transform:scale(0.5);-ms-transform:scale(0.5);transform:scale(0.5);-webkit-transform-origin:0 0;-ms-transform-origin:0 0;transform-origin:0 0;padding:1px;border-radius:200px;pointer-events:none
}
}
.reg-btn.reg-btn-red{background:#ff5a60;color:#fff;border:1px solid #ff5a60;border-radius:100px
}
@media screen and (-webkit-min-device-pixel-ratio: 2){
.reg-btn.reg-btn-red{position:relative;border:0
}
.reg-btn.reg-btn-red:before{content:"";width:200%;height:200%;position:absolute;top:0;left:0;border:1px solid #ff5a60;-webkit-transform:scale(0.5);-ms-transform:scale(0.5);transform:scale(0.5);-webkit-transform-origin:0 0;-ms-transform-origin:0 0;transform-origin:0 0;padding:1px;border-radius:200px;pointer-events:none
}
}
.reg-btn.bg-white{background:#fff;color:#00a0a6
}
a.color-white{color:#fff
}
::-webkit-input-placeholder{color:#ddd
}
.progress-content{height:3px;overflow:hidden;margin-top:5px;display:-webkit-flex;display:-ms-flexbox;display:flex;width:100%
}
.progress-content .step-progress{-webkit-flex:1;-ms-flex:1;flex:1;display:block;text-align:center
}
.progress-content .step-progress{background-color:#00b9c0;height:100%;border-left:2px solid #00a0a6;position:relative
}
.progress-content .step-progress:first-child{border-left:none
}
.progress-content .step-progress:before{position:absolute;content:'';height:100%;display:block;width:0;transition:all .2s linear;background-color:#fff
}
.progress-content .step-progress.active:before{width:100%
}
.txt-alert{color:#d6d5d5;margin-top:3px
}
.transition{position:absolute;top:0
}
.address-txt{display:block;height:100%;font-size:16px;line-height:30px
}
#app{max-width:750px;margin:0 auto;overflow:hidden
}
.flexbox{display:-webkit-flex;display:-ms-flexbox;display:flex;width:100%
}
.flexbox .flex{-webkit-flex:1;-ms-flex:1;flex:1;display:block;text-align:left
}
.gray-bg{background-color:#eaeaea
}
.reg-bg{background-color:#00a0a6;color:#fff
}
.register-block .child-view{left:0;padding:0 20px
}
.child-view{position:absolute;top:0;width:100%
}
.child-view,.transition{transition:all 0.5s cubic-bezier(0.55, 0, 0.1, 1)
}
.slide-left-enter,.slide-right-leave-active{opacity:0;-webkit-transform:translate(30px, 0);-ms-transform:translate(30px, 0);transform:translate(30px, 0)
}
.slide-left-leave-active,.slide-right-enter{opacity:0;-webkit-transform:translate(-30px, 0);-ms-transform:translate(-30px, 0);transform:translate(-30px, 0)
}



